<template>
  <div class="vu-user">
    <VuRichUserTooltip
      v-if="showUserTooltip"
      :user="user"
      :disabled-actions="disabledActions"
      :hidden-actions="hiddenActions"
      :attach="attach"
      v-on="listeners.vOn || {}"
    >
      <VuUserPicture
        v-if="showPicture"
        :id="user.id"
        :src="user.imgSrc"
        :presence="user.presence"
        :clickable="clickable"
        :style="{ background: pictureBackground }"
        @click="(e) => $emit('click-user', value)"
      />
      <VuUserName
        v-if="showName"
        :first-name="user.firstName"
        :last-name="user.lastName"
        :clickable="clickable"
        :shift="showPicture"
        @click="(e) => $emit('click-user', e)"
      >
        <slot name="userName" />
      </VuUserName>
    </VuRichUserTooltip>
    <template v-else>
      <VuUserPicture
        v-if="showPicture"
        :id="user.id" 
        :src="user.imgSrc"
        :presence="user.presence"
        :clickable="clickable"
        :style="{ background: pictureBackground }"
        @click="(e) => $emit('click-user', e)"
      />
      <VuUserName
        v-if="showName"
        :first-name="user.firstName"
        :last-name="user.lastName"
        :clickable="clickable"
        :shift="showPicture"
        @click="(e) => $emit('click-user', e)"
      >
        <slot name="userName" />
      </VuUserName>
    </template>
  </div>
</template>

<script>

import { getListenersFromAttrs } from '../../utils/vue';
import validateAttachTarget from '../../utils/validateAttachTarget';
import { validate as personValidator } from '../../model/person';
import { validate as userActionsValidator } from '../../model/userActionsValidator';
import VuRichUserTooltip from './vu-rich-user-tooltip.vue';
import VuUserPicture from './vu-user-picture.vue';
import VuUserName from './vu-user-name.vue';

export default {
  name: 'vu-user',
  emits: ['click-other-user', 'click-user'],
  props: {
    user: {
      type: Object,
      required: true,
      validator: personValidator,
    },
    disabledActions: {
      type: Array,
      required: false,
      default: () => [],
      validator: userActionsValidator,
    },
    hiddenActions: {
      type: Array,
      required: false,
      default: () => [],
      validator: userActionsValidator,
    },
    showPicture: {
      type: Boolean,
      required: false,
      default: true,
    },
    showName: {
      type: Boolean,
      required: false,
      default: false,
    },
    showUserTooltip: {
      type: Boolean,
      required: false,
      default: true,
    },
    clickable: {
      type: Boolean,
      required: false,
      default: true,
    },
    pictureBackground: {
      type: String,
      required: false,
      default: '#fff',
    },
    attach: {
      default: () => false,
      validator: validateAttachTarget,
    },
  },
  computed: {
    listeners() {
      return getListenersFromAttrs(this.$attrs, true);
    },
  },
  data: () => ({
    getListenersFromAttrs,
  }),
  components: { VuRichUserTooltip, VuUserPicture, VuUserName, VuUserPicture }
};

</script>
<style scoped lang="scss">
  .vu-user {
    & ::v-deep(.vu-popover__activator),
    & {
      display: flex;
      align-items: center;
    }

    .vu-avatar {
      flex: 0 0 auto;
    }

    .vu-user-name {
      flex: 0 1 auto;
      height: calc(100% - 0.2em)
    }
  }
</style>
