<template>
  <template v-if="isVideo">
    <div
      class="vu-media-upload-preview__video-container"
      :style="videoSizer"
    >
      <video
        class="vu-media-upload-preview"
        :src="src"
        :controls="videoControls"
      />
    </div>
  </template>
  <VuImage
    v-else-if="!isVideo"
    class="vu-media-upload-preview"
    :aspect-ratio="displayRatio"
    :src="src"
    contain
    style="height: 100%;"
  >
    <div
      class="vu-media-upload-preview__delete-icon"
      @click="$emit('delete')"
    >
      <VuIconBtn :icon="deleteIcon" />
    </div>
  </VuImage>
  <vu-spinner v-else-if="loading" />
  <div
    v-if="isVideo"
    class="vu-media-upload-preview__delete-icon"
    @click="$emit('delete')"
  >
    <VuIconBtn :icon="deleteIcon" />
  </div>
</template>
<script>
import VuIconBtn from './vu-icon-btn.vue';
import VuImage from './vu-image.vue';

export default {
  name: 'vu-media-upload-preview',
  computed: {
    videoSizer() {
      const [width, height] = this.displayRatio?.replace(',', '').split('/');
      const ratio = Number(width) / Number(height);
      return ratio ? { paddingBottom: `${(1 / ratio) * 100}%` } : undefined;
    },
  },
  props: {
    deleteIcon: {
      type: String,
      default: () => 'trash',
    },
    src: {
      type: String,
      required: true,
    },
    isVideo: {
      type: Boolean,
    },
    videoControls: {
      type: Boolean,
      required: false,
    },
    displayRatio: {
      type: String,
      default: () => '16 / 9',
    },
  },
  emits: ['delete'],
  components: { VuImage, VuIconBtn }
};
</script>
<style lang="scss">
@import '../styles/variables';

.vu-media-upload-preview.vu-image {
  --vu-image-background-fill-color: #{$grey-1};
}
</style>
<style scoped lang="scss">
@import '../styles/variables';
.vu-media-upload-preview {
  background-color: $grey-1;
}

.vu-media-upload-preview__video-container {
  position: relative;
  height: 0;
  width: 100%;
  
  video {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.vu-media-upload-preview__delete-icon {
  position: absolute;
  top: 0;
  right: 0;
  background: $grey-1;
  border-radius: 0px 3px 0px 4px;
  z-index: 2;
}
</style>
