<template>
  <div
    class="vu-media-upload"
    :class="[{ 'has-error': error, 'vu-media-upload--border': !hasLabel, 'vu-media-upload--inner-flex': hasLabel }]"
    :style="!hasLabel ? wrapStyle : {}"
  >
    <label
      v-if="hasLabel"
      class="control-label"
    >
      <VuIcon
        v-if="icon"
        :icon="icon"
      />
      <slot name="label">{{ label }}<span
        v-if="required"
        class="label-field-required"
      > *</span> </slot>
    </label>
    <input
      ref="upload-input"
      type="file"
      name="upload"
      style="display: none"
      @change="(e) => selectFiles($refs['upload-input'].files)"
      :multiple="multiple"
    >
    <div
      class="vu-media-upload__inner"
      ref="inner"
      :style="hasLabel ? wrapStyle : ''"
      :class="{ 'vu-media-upload--border': hasLabel, 'full-height': !hasLabel }"
      @dragover.prevent="dragOver()"
      @dragenter.prevent="dragOver()"
      @dragleave="dragLeave"
      @dragend="dragLeave"
    >
      <VuMediaUploadDroppable
        v-if="dragged"
        :valid-drop="allowDrop"
        @drop="onFileDrop"
      >
        <template #drop-icon>
          <slot name="drop-icon" />
        </template>
        <template #drop-label>
          <slot name="drop-label" />
        </template>
      </VuMediaUploadDroppable>
      <template v-if="status === states.empty">
        <slot
          name="empty"
          :input="$refs['upload-input']"
        >
          <VuMediaUploadEmpty
            @browse="
              $refs['upload-input'].value = '';
              $refs['upload-input'].click();
            "
          />
        </slot>
      </template>
      <template v-else-if="status === states.loading">
        <slot name="loading">
          <VuMediaUploadLoading
            :progress="uploadProgress"
            @upload-abort="$emit('upload-abort')"
          />
        </slot>
      </template>
      <template v-else-if="status === states.error">
        <slot name="error">
          <VuMediaUploadError
            @retry="onRetry"
            v-bind="{ errorBucket }"
          />
        </slot>
      </template>
      <template v-else-if="status === states.complete">
        <slot name="preview">
          <vuMediaUploadPreview
            v-bind="preview"
            @delete="
              $emit('delete');
              status = states.empty;
            "
          />
        </slot>
      </template>
    </div>
  </div>
</template>
<script>
import inputable from '../mixins/inputable';
import { RegistrableInput } from '../mixins/registrable';
import validatable from '../mixins/validatable';
import loadable from '../mixins/loadable';
import disablable from '../mixins/disablable';
import states from './mediaUploadStates';

import VuIcon from './vu-icon.vue';
import VuMediaUploadDroppable from './vu-media-upload-droppable.vue';
import VuMediaUploadLoading from './vu-media-upload-loading.vue';
import VuMediaUploadError from './vu-media-upload-error.vue';
import VuMediaUploadEmpty from './vu-media-upload-empty.vue';
import VuMediaUploadPreview from './vu-media-upload-preview.vue';

export default {
  name: 'vu-media-upload',
  mixins: [inputable, disablable, loadable, validatable, RegistrableInput],
  props: {
    icon: {
      type: String,
      default: () => '',
    },
    mediaUrl: {
      type: String,
      default: () => '',
    },
    video: {
      type: Boolean,
      default: false,
    },
    videoControls: {
      type: Boolean,
      default: true,
    },
    uploadProgress: {
      type: Number,
      required: false,
      default: undefined,
    },
    fileMaxSize: {
      type: Number,
      default: () => Infinity,
    },
    displayRatio: {
      type: String,
      default: () => '16 / 9',
    },
    showLabel: {
      type: Boolean,
    },
    multiple: {
      type: Boolean,
    },
    allowLoadingDrop: {
      type: Boolean,
    },
    allowErrorDrop: {
      type: Boolean,
    },
    skipTypeCheck: {
      type: Boolean,
      required: false,
    },
    noDragNDrop: {
      type: Boolean,
      required: false,
    },
    acceptVideo: Boolean,
    acceptImage: {
      type: Boolean,
      default: true,
    },
    state: {
      type: String,
      default: '',
    },
  },
  inject: {
    vuMediaUploadSizeExcess: {
      default: 'File exceeds maximum size.',
    },
    vuMediaUploadShouldBeImage: {
      default: 'Please select an image.',
    },
    vuMediaUploadShouldBeVideo: {
      default: 'Please select a video.',
    },
  },
  data() {
    return {
      states,
      innerState: 'empty',
      innerVideo: false,
      allowDrop: false,
      dragged: false,
      error: '',
    };
  },
  created() {
    this.localRules = [this.checkVideoType, this.checkImgType, this.checkFileSize];
  },
  emits: ['update:state', 'upload-abort', 'select', 'delete', 'retry'],
  computed: {
    preview() {
      return {
        src: this.mediaUrl,
        isVideo: this.video || this.innerVideo,
        displayRatio: this.displayRatio,
        videoControls: this.videoControls,
      };
    },
    hasLabel() {
      return this.showLabel && !this.multiple;
    },
    wrapStyle() {
      return {
        'aspect-ratio': this.displayRatio,
      };
    },
    status: {
      get() {
        return this.state || this.innerState;
      },
      set(val) {
        this.$emit('update:state', val);
        this.innerState = val;
      },
    },
  },
  watch: {
    hasError(v) {
      if (v) {
        this.status = this.states.error;
      }
    },
  },
  methods: {
    selectFiles(files) {
      if (this.multiple && files.length > 1) {
        this.status = this.states.loading;
        this.$emit('select', files);
      }
      else if (this.skipTypeCheck) {
        this.status = this.states.loading;
        this.$emit('select', files);
      }
      else if (this.validate(files[0])) {
        this.status = this.states.loading;
        this.$emit('select', files);
      }
    },
    dragOver() {
      if (this.noDragNDrop) {
        return;
      }
      if (this.state === this.states.complete)
        return;
      if (this.state === this.states.loading && !this.allowLoadingDrop)
        return;
      if (this.state === this.states.error && !this.allowErrorDrop)
        return;
      this.allowDrop = true;
      this.dragged = true;
    },
    dragLeave(e) {
      if (e.currentTarget.contains(e.relatedTarget)) {
        return;
      }
      this.dragged = false;
      this.allowDrop = false;
    },
    onFileDrop(e) {
      this.dragged = false;
      this.allowDrop = false;
      this.status = this.states.loading;
      this.selectFiles(e.dataTransfer.files);
    },
    checkFileSize({ size }) {
      if (this.fileMaxSize && size / 1024 / 1024 >= this.fileMaxSize) {
        return this.vuMediaUploadSizeExcess;
      }
      return true;
    },
    /* 3 checks disablable with skipTypeCheck */
    checkImgType({ type }) {
      if (this.acceptImage) {
        const isImage = /image\/(jpg|jpeg|png|webp)$/i.test(type);
        if (isImage)
          this.innerVideo = false;
        if (!this.acceptVideo) {
          return isImage || this.vuMediaUploadShouldBeImage;
        }
      }
      return true;
    },
    checkVideoType({ type }) {
      if (this.acceptVideo) {
        const isVideo = /video\/(mp4|avi)$/i.test(type);
        this.innerVideo = isVideo;
        if (!this.acceptImage) {
          return isVideo || this.vuMediaUploadShouldBeVideo;
        }
      }
      return true;
    },
    checkVideoAndImgType({ type }) {
      if (this.acceptVideo && this.acceptImage) {
        return (/video\/(mp4|avi)$/i.test(type) && /image\/(jpg|jpeg|png|webp)$/i.test(type)) || this.vuMediaUploadTypeUnexpected;
      }
      return true;
    },
    onRetry() {
      this.errorBucket = [];
      this.status = this.states.empty;
      this.$emit('retry', this.$refs['upload-input'].value);
    },
  },
  components: { VuIcon, VuMediaUploadDroppable, VuMediaUploadLoading, VuMediaUploadError, VuMediaUploadEmpty, VuMediaUploadPreview }
};
</script>
<style scoped lang="scss">
@import '../styles/variables';

.vu-media-upload {
  min-width: 250px;
  min-height: 145px;
  
  &--inner-flex {
    display: flex;
    flex-direction: column;

    .vu-media-upload__inner {
      flex: 1 0 auto;
    }
  }

  .vu-media-upload__inner {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }

  .full-height {
    height: 100%;
  }

  &--border {
    padding: 5px;
    border: 1px solid #d1d4d4;
    border-radius: 4px;
    display: flex;
    flex-direction: column;
  }
}
</style>
