<template>
  <div
    class="vu-thumbnail item" 
    :class="classes"
  >
    <div
      class="thumbnail-wrap"
      style="position: relative"
    >
      <VuImage
        :src="src"
        :lazy="vuCollectionLazyImages"
        :aspect-ratio="'200/150'"
        contain
      />
      <div
        v-if="active"
        class="thumbnail__thumb"
      />
      <VuIcon
        v-if="selectable || selected"
        icon="check"
        class="thumbnail__check"
      />
      <VuTile
        thumbnail
        :title="title"
        :type="type"
        :author="author"
        :date="date"
        :actions="actions"
        :custom-meta-data="customMetaData"
        hide-status-bar
        @click-action="getListenersFromAttrs($attrs)['onClickAction']"
      />
      <div class="thumbnail__content">
        {{ text }}
      </div>
      <VuStatusBar
        v-if="!hideStatusBar"
        :status="status"
      />
    </div>
  </div>
</template>
<script>
import { getListenersFromAttrs } from '../../utils/vue';
import VuIcon from '../vu-icon.vue';
import VuImage from '../vu-image.vue';
import VuStatusBar from './vu-status-bar.vue';
import VuTile from './vu-tile.vue';

export default {
  name: 'vu-thumbnail',
  inject: ['vuCollectionLazyImages'],
  props: {
    /* eslint-disable vue/require-default-prop */
    id: {
      type: String,
      required: true,
    },
    src: String,
    type: String,
    active: Boolean,
    actions: Array,
    title: String,
    text: String,
    selected: Boolean,
    selectable: Boolean,
    author: String,
    date: Date,
    customMetaData: String,
    status: Array,
    hideStatusBar: Boolean,
  },
  data: () => ({
    getListenersFromAttrs,
  }),
  computed: {
    classes() {
      return {
        'thumbnail--selectable': this.selectable || this.selected,
        'thumbnail--selected': this.selected,
        'thumbnail--active': this.active,
      };
    },
  },
  components: { VuImage, VuIcon, VuTile, VuStatusBar }
};
</script>
<style lang="scss" scoped>
  @import 'select';

  $name: 'thumbnail';

  @include selectable(#{$name}, 'wall');
  @include active(#{$name});

  .vu-#{$name} {
    .thumbnail-wrap {
      display: flex;
      flex-direction: column;
    }

    .vu-image {
      flex: 0 0 auto;
      width: 100%;
      border-bottom: 1px solid $grey-3;
    }

    .vu-tile + .thumbnail__content {
      border-top: 1px solid $grey-3;
    }

    .thumbnail__content {
      flex: 0 0 auto;
      border-top: 1px solid $grey-3;
      padding: 14px 10px;
      min-height: 34px;
      font-size: 12px;
    }

    .vu-tile {
      flex: 0 0 70px;
      width: 100%;
      border: none;
    }

    .vu-status-bar {
      border-top: 1px solid $grey-3;
    }

    min-width: 200px;
    max-width: 420px;

    :deep(.vu-tile.tile--thumbnail) {
      min-width: 200px;
    } 
  }
</style>
<style lang="scss">
  @import '../../styles/variables';

  $name: 'thumbnail';

  .vu-#{$name} {
    --vu-thumbnail-check-top: 10px;
    --vu-thumbnail-check-right: 10px;
  };
</style>
