<template>
  <div
    v-if="keepRendered || innerShow"
    v-show="innerShow"
  >
    <template v-if="!noMobile && mobileWidth">
      <VuMobileDialog
        v-bind="{
          ...pick($props, 'title'),
          ...pickNRename(
            $props,
            { key: 'mobileBackIcon', newName: 'backIcon' },
            { key: 'mobileBackIconTooltip', newName: 'backIconTooltip' },
            { key: 'mobileCustomBackIcon', newName: 'customBackIcon' },
            { key: 'mobileNextIcon', newName: 'nextIcon' },
            { key: 'mobileNextIconTooltip', newName: 'nextIconTooltip' },
            { key: 'mobileNextIconDisabled', newName: 'nextIconDisabled' },
            { key: 'mobileCustomNextIcon', newName: 'customNextIcon' },
            { key: 'mobileScrollable', newName: 'scrollable' }
          ),
          disabled: valid,
        }"
        @close="cancel()"
        @confirm="confirm()"
      >
        <template #mobile-dialog-header>
          <slot name="mobile-header" />
        </template>
        <template #default>
          <slot name="modal-body">
            <div
              v-if="rawContent"
              v-html="rawContent"
            />
            <p v-else-if="message">
              {{ message }}
            </p>
            <VuForm
              ref="form"
              v-if="showInput"
            >
              <VuInput
                :label="label"
                :required="required"
                :helper="helper"
                :success="success"
                :placeholder="placeholder"
                :rules="rules"
                v-model="model"
              />
            </VuForm>
          </slot>
        </template>
      </VuMobileDialog>
    </template>
    <template v-else>
      <div
        class="vu-modal modal modal-root"
        style="display: block"
      >
        <div class="modal-wrap">
          <div
            class="modal-content"
            @keyup.enter="() => { if(keyboard && (!showInput || $refs.form.validate()) ) { confirm() } }"
            @keyup.escape="() => { if(keyboard) { showCancelButton ? cancel() : close() }}"
          >
            <div class="modal-header">
              <slot name="modal-header">
                <span
                  v-if="showCancelIcon"
                  @click="cancel(true)"
                  class="close fonticon fonticon-cancel"
                  title=""
                />
                <h4>{{ title }}</h4>
              </slot>
            </div>
            <div class="modal-body">
              <slot name="modal-body">
                <div
                  v-if="rawContent"
                  v-html="rawContent"
                />
                <p v-else-if="message">
                  {{ message }}
                </p>
                <VuForm
                  ref="form"
                  v-if="showInput"
                >
                  <VuInput
                    :label="label"
                    :required="required"
                    :helper="helper"
                    :success="success"
                    :placeholder="placeholder"
                    :rules="rules"
                    v-model="model"
                  />
                </VuForm>
              </slot>
            </div>
            <div
              v-if="showFooter"
              class="modal-footer"
            >
              <slot name="modal-footer">
                <VuBtn
                  color="primary"
                  @click="confirm"
                >
                  {{ okLabel }}
                </VuBtn>
                <VuBtn
                  v-if="showCancelButton"
                  color="default"
                  @click="cancel()"
                >
                  {{ cancelLabel }}
                </VuBtn>
              </slot>
            </div>
          </div>
        </div>
      </div>
      <div class="modal-overlay in" />
    </template>
  </div>
</template>
<script>
import showable from '../../mixins/showable';
import { pick, pickNRename } from '../../utils/pick';
import VuBtn from '../vu-btn.vue';
import VuForm from '../vu-form.vue';
import VuInput from '../vu-input.vue';
import VuMobileDialog from './vu-mobile-dialog.vue';

export default {
  name: 'vu-modal',
  data: () => ({
    model: '',
    mobileWidth: false,
    resizeObs: {},
    pick,
    pickNRename,
  }),
  emits: ['close', 'cancel', 'confirm'],
  mixins: [showable],
  props: {
    show: {
      type: Boolean,
      required: false,
      default: () => false,
    },
    keepRendered: {
      type: Boolean,
      default: () => false,
    },
    title: {
      type: String,
      default: () => '',
    },
    message: {
      type: String,
      default: () => '',
    },
    rawContent: {
      type: String,
      default: '',
    },
    keyboard: {
      type: Boolean,
      default: () => true,
    },
    showCancelIcon: {
      type: Boolean,
      default: () => true,
    },
    showCancelButton: {
      type: Boolean,
      default: () => false,
    },
    showFooter: {
      type: Boolean,
      default: () => true,
    },
    showInput: {
      type: Boolean,
      default: () => false,
    },
    /* input props */
    label: {
      type: String,
      default: () => '',
    },
    helper: {
      type: String,
      default: () => '',
    },
    placeholder: {
      type: String,
      default: () => '',
    },
    color: {
      type: String,
      default: () => '',
    },
    rules: {
      type: Array,
      default: () => [],
    },
    required: {
      type: Boolean,
      default: () => true,
    },
    success: {
      type: Boolean,
      default: () => false,
    },
    /* input props */
    cancelLabel: {
      type: String,
      default: () => 'Cancel',
    },
    okLabel: {
      type: String,
      default: () => 'OK',
    },
    /* mobile specific props */
    noMobile: {
      type: Boolean,
    },
    /* eslint-disable vue/require-default-prop */
    mobileNextIcon: {
      type: String,
    },
    mobileNextIconTooltip: {
      type: String,
    },
    mobileCustomNextIcon: {
      type: Boolean,
    },
    mobileNextIconDisabled: {
      type: Boolean,
    },
    mobileBackIcon: {
      type: String,
    },
    mobileBackIconTooltip: {
      type: String,
    },
    mobileCustomBackIcon: {
      type: Boolean,
    },
    mobileScrollable: {
      type: Boolean,
    },
    /* cancel */
    // eslint-disable-next-line vue/prop-name-casing
    _cancel: Boolean,
  },
  inject: {
    vuMobileBreakpoint: {
      default: () => '640',
    },
  },
  watch: {
    _cancel(value) {
      if (value)
        this.cancel();
    },
  },
  beforeMount() {
    if (!this.noMobile) {
      this.checkWidth();
      window.addEventListener('resize', this.checkWidth);
    }
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.checkWidth);
  },
  methods: {
    cancel(fromIcon = false) {
      this.innerShow = false;
      this.$emit(fromIcon ? 'close' : 'cancel');
      if (this.showInput)
        this.clear();
    },
    confirm() {
      if (!this.showInput) {
        this.$emit('confirm', true);
        this.innerShow = false;
      }
      else if (this.$refs.form.validate()) {
        this.$emit('confirm', this.model);
        this.innerShow = false;
        this.clear();
      }
    },
    validate(blank) {
      this.$refs.form.validate(blank);
    },
    clear() {
      this.model = '';
    },
    checkWidth() {
      if (window.document.documentElement.clientWidth < this.vuMobileBreakpoint) {
        this.mobileWidth = true;
      }
      else {
        this.mobileWidth = false;
      }
    },
  },
  components: { VuMobileDialog, VuInput, VuForm, VuBtn }
};
</script>
<style>
.vu-modal .form-group {
  margin-bottom: 0px;
}
</style>
