<template>
  <ul
    class="vu-select-options"
    :class="{ 'select-options--multiple': multiple, 'select-options--single': !multiple, 'select-options--user': user }"
  >
    <li
      v-if="!multiple && placeholder"
      :class="{ 'option--selected': selected[0].value === undefined }"
      class="option__placeholder"
      @click="$emit('click-item', { value: '' } )"
    >
      <span class="option__text">{{ placeholder }}</span>
    </li>
    <li
      v-for="item in options"
      :key="`${item.id || uuidv4()}`"
      :class="{ 'option--selected': (item.selected || selected.includes(item)) }"
      :disabled="item.disabled"
      @click="!item.disabled && $emit('click-item', item)"
    >
      <template v-if="user">
        <div class="flex items-center">
          <VuUserPicture
            size="small"
            :id="item.value"
            :src="item.src"
          /><span class="option__text">{{ item.text || item.label }}</span>
        </div>
      </template>
      <slot
        v-else
        :item="item"
      >
        <VuIcon
          v-if="item.fonticon"
          :icon="item.fonticon"
        />
        <span class="option__text">{{ item.text || item.label }}</span>
      </slot>
    </li>
  </ul>
</template>

<script>
import { uuidv4 } from '../utils/guid';
import VuIcon from './vu-icon.vue';
import VuUserPicture from './vu-user/vu-user-picture.vue';

export default {
  name: 'vu-select-options',
  props: {
    options: {
      type: Array,
      required: true,
    },
    multiple: {
      type: Boolean,
      required: false,
    },
    user: {
      type: Boolean,
      required: false,
    },
    selected: {
      type: Array,
      required: true,
    },
    placeholder: {
      type: String,
      default: () => '',
    },
  },
  emits: ['click-item'],
  data: () => ({
    uuidv4,
  }),
  components: { VuIcon, VuUserPicture }
};
</script>
<style scoped lang="scss">
@import '../styles/_variables'; 

.vu-select-options { 
  li {
    margin: 0;
    padding: 0;
    background: none;
    list-style: none;
    cursor: pointer;
    width: 100%;
  }
}

ul.vu-select-options:not(.brute) { padding-inline-start: 0 }
.vu-select-options:not(.brute) {
  $base: #{&};

  li {
    & {
      font-size: 14px;
      padding-left: 14px;
      padding-right: 14px;
      height: 38px;
      border-bottom: 1px solid $grey-4;
    }

    > .option__text {
      line-height: 38px;
    }

    &:last-child {
      border-bottom: none;
    }

    &[disabled] {
      color: $grey-5;
      cursor: var(--vu-select-options-disabled-pointer, default);
    }

    &:not([disabled]):hover {
      background: $grey-2;
    }
  }

  &.select-options--single li:not([disabled])  {
    &.option__placeholder {
      background: #F1F1F1
    }

    &.option--selected, 
    &:hover {
      background: $blue-3;
      color: white;
    }
  }

  &.select-options--multiple li:not([disabled]) {
    &.option--selected {
      background: $grey-0;
      color: $grey-7;
      border-left: 5px solid $blue-3;
      padding-left: 9px;
    }

    &:hover {
      background: $blue-3;
      color: white;
      border-bottom-color: $blue-3;
    }
  }
  &.select-options--multiple li {
    border-bottom: 1px solid $grey-1;
  }

  &.select-options--user li {
    padding-top: 6px;

    div.flex {
      display: flex;
    }
    div.items-center {
      align-items: center;
    }
  }
}
</style>
